body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.4;
}

html {
  margin: 0;
  padding: 0;
  width: 100%;
}


.dont-select-text {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.text-app {
  color: #404040;
  line-height: 1.55;
}

.header-menu:hover .header-submenu {
  display: block;
  overflow: hidden;
}

.header-submenu-item:hover{
  color: #fa1081;
}

.header-submenu-item{
    cursor: pointer;
  color: #000;
  margin-top: 10px;
  text-decoration: none;
}

.header-submenu1-item:hover{
  color: #fa1081;
}

.header-submenu1-item{
  font-size: 14px;
  cursor: pointer;
  color: #000;
  margin-top: 10px;
  text-decoration: none;
}

.header-submenu{
  border: 1px solid #fa1081;
  border-radius: 3px;
  display: none;
  position: absolute;
  background: white;
  z-index: 9999;
  padding: 20px;
  max-width: 120px;
  text-align: left;
    bottom: 20px;
  left: 20px;
}

.pointer{
  cursor: pointer;
}

.pointer:hover{
  color: #fa1081 !important;
}


.resizer{
  cursor: pointer;
  position: absolute;
}
@media (min-width: 1000px) {
  .textlinestandart {
    font-size: 16px;
  }
  .textlinebig {
  @include text-app;
    font-size: 30px;
    font-weight: 700;
  }
  .resizer{
    width: 30px;
    height: 30px;
  }
  .imagegallery-museum-carousel{
    width: 900px;
    display: inline-block;
  }
  .headline {
  @include text-app;
    font-size: 35px;
    font-weight: 900;
    text-align: center;
  }
}

@media (max-width: 1000px) {
  .textlinestandart {
    font-size: 14px;
  }
  .textlinebig {
  @include text-app;
    font-size: 20px;
    font-weight: 700;
  }
  ::-webkit-scrollbar {
    display: none;
  }
  .resizer{
    width: 25px;
    height: 25px;
  }
  .imagegallery-museum-carousel{
    width: 100%;
  }
  .headline {
  @include text-app;
    font-size: 20px;
    font-weight: 700;
    text-align: center;
    margin-left: 50px;
    margin-right: 50px;
  }
}

.link-white:hover, .link:hover{
  color: #dac390;
}

.link-white{
  color: #fff;
  text-decoration: none;
}

.link{
  text-decoration: none;
}

.link-black{
  color: #000;
  text-decoration: none;
}

.left1{
  text-align: left;
}

.header-menu1:hover .header-submenu1 {
  display: block;
  overflow: hidden;
}

.header-submenu1{
  max-height: 160px;
  border: 1px solid #fa1081;
  border-radius: 3px;
  display: none;
  position: absolute;
  background: white;
  z-index: 9999;
  padding: 10px;
  max-width: 120px;
  text-align: left;
  top: 5px;
  left: 0px;
}
.link-kategory{
    text-align: left;
    height: 20px !important;
    overflow: hidden;
}

@media (max-width: 1000px) {
    .title-kategory{
        width: 300px !important;
        text-align: center;
    }
  .item-size{
    left: calc(50% - 225px + 118px) !important;
    width: 200px !important;
    text-align: center;
  }
    .title-kategory1{
        font-size: 22px !important;
    }
  .status-order{
    left: calc(50% - 160px + 15px) !important;
  }

}

@media (min-width: 1000px) {
  .item-size{
    left: calc(50% - 765px + 924px) !important;
    width: 400px !important;
    text-align: center;
  }
    .title-kategory{
        width: 850px !important;
        text-align: center;
    }
  .text1{
    text-align: left;
    margin-left: calc(50% - 310px);
    margin-right: calc(50% - 310px);
    width: 620px;
    margin-bottom: 50px;

  }

}

@media (max-width: 600px) {
  .text1{
    text-align: left;
    margin-left: 15px;
    margin-right: 15px;
    margin-bottom: 50px;
  }

  .cart-text{
    left: calc(100% - 140px) !important;
  }

  .size-item{
    width: 100% !important;
    left: 0 !important;
  }

  .buy12{
    width: 100% !important;
    left: 0 !important;
  }

}

@media (min-width: 600px) {
  .buy12{
    width: 100% !important;
    left: 0 !important;
  }

}



@media screen and (min-width: 980px){
  .kategory2 {
    width: 125px !important;
  }
  .t-records {
     opacity: 1!important;
  }
}












.image {
  overflow:hidden;
}
.image img {
  -moz-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
  -webkit-transition: all 0.5s ease-out;
}

.image img:hover{
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -o-transform: scale(1.1);
}
